import Web3 from 'web3'
import abi from './abis/erc20.json';
import SmartContract from './SmartContract'

import { toBN } from 'web3-utils'

const ADDRESS = {
  1: "0x89d24a6b4ccb1b6faa2625fe562bdd9a23260359",
  42: "0xC4375B7De8af5a38a93548eb8453a498222C4fF2"
}

const DECIMALS = toBN(10).pow(18)
const ZERO = toBN(0)

export class TokenDai extends SmartContract {
  static async build(web3: Web3): Promise<TokenDai> {
    const networkId = await web3.eth.net.getId()
    const networks = {
      [networkId]: {
        address: ADDRESS[networkId],
        transactionHash: ''
      }
    }

    const builder = TokenDai.configure({ contractName: 'TokenDai', abi, networks })
    const options = await builder(web3)

    return new TokenDai(options)
  }

  async getBalance(account: string) {
    const balance = await this.contract.methods.balanceOf(account).call()

    return toBN(balance).div(DECIMALS) || ZERO;
  }
}


export default TokenDai
